<template>
  <div class="section">
    <section class="container section-spacing">
      <div class="columns is-centered">
        <div class="column is-8">
          <h1 class="title is-1">
            Ticket
            <span class="has-text-primary">Purchased</span>
          </h1>
        </div>
      </div>
    </section>

    <section class="container section-spacing content">
      <div class="columns is-centered">
        <div class="column is-7">
          <p class="is-size-4">
            Congratulations
            <span class="has-text-primary">{{ personName }}</span>
          </p>
          <p v-if="invitedBy && paidFor">
            You now have a ticket to TEDxAucklandSalon IN THE DARK! Your name
            will be on the door, so no need to bring a ticket along.
            {{ invitedByName }} should have a receipt confirmation.
          </p>
          <p v-else>
            You now have a ticket to TEDxAucklandSalon IN THE DARK! Your name
            will be on the door, so no need to bring a ticket along. You should
            receive a receipt through email confirm your purchase.
          </p>
          <ul>
            <li><strong>When:</strong> Saturday August 31st (6pm till late)</li>
            <li><strong>Where:</strong> Dans le Noir ? Rydges Auckland</li>
          </ul>
          <p>
            If you need to update any special requirements please do so below.
          </p>
          <p>
            We look forward to hearing you there!
          </p>
          <p>
            <strong class="has-text-primary">
              TEDxAuckland
            </strong>
          </p>
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-6">
          <form @submit.prevent="updateInfo">
            <fieldset>
              <legend class="label">
                Dietary Requirements
              </legend>
              <div class="checkbox-row">
                <b-checkbox
                  v-for="requirement in dietaryRequirements"
                  :key="requirement"
                  class="checkbox-column"
                  v-model="person.data.dietaryRequirements"
                  :native-value="requirement"
                >
                  {{ requirement }}
                </b-checkbox>
              </div>
            </fieldset>

            <fieldset>
              <legend class="label">
                Accessability Requirements
              </legend>
              <div class="checkbox-row">
                <b-checkbox
                  v-for="requirement in accessabilityRequirements"
                  :key="requirement"
                  class="checkbox-column"
                  v-model="person.data.accessabilityRequirements"
                  :native-value="requirement"
                >
                  {{ requirement }}
                </b-checkbox>
              </div>
            </fieldset>

            <input
              class="update-button button is-medium is-pulled-right"
              type="submit"
              value="Update info"
            />
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import ProfileService from "@/services/ProfileService";
import NProgress from "nprogress";
import { personNameMixin } from "@/mixins/personNameMixin";
import { required, email } from "vuelidate/lib/validators";
import { mapGetters } from "vuex";

export default {
  name: "TicketOfferPurchased",
  mixins: [personNameMixin],
  validations: {
    person: {
      firstName: { required },
      lastName: { required },
      email: { required, email }
    }
  },
  computed: {
    ...mapGetters(["dietaryRequirements", "accessabilityRequirements"]),
    ...mapGetters({
      event: "events/event",
      person: "people/person",
      ticketOffer: "ticketOffers/ticketOffer"
    }),
    paidFor() {
      return this.ticketOffer.invitePaidFor;
    },
    invitedBy() {
      return this.ticketOffer.invitedBy;
    },
    invitedByName() {
      return `${this.invitedBy.firstName} ${this.invitedBy.lastName}`;
    }
  },
  methods: {
    updateInfo() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();

        ProfileService.updateProfile(this.person.id, {
          profile: this.person.data
        })
          .then(() => {
            NProgress.done();
            this.$snackbar.open(`Successfully updated special requirements`);
          })
          .catch(e => {
            NProgress.done();
            console.log("ERROR", e);
          });
      }
    }
  }
};
</script>

<style scoped>
.update-button {
  margin-top: 2.3rem;
}
</style>
